import { Watchlist } from "@rp/common/api-types"
import { EllipsisTooltip } from "@rp/react/web/common/composite-components"
import { Box } from "@rp/react/web/mui/core"
import { Edit } from "@rp/react/web/mui/icons"

const WatchlistName = ({
  canEdit,
  watchlist,
  rename,
}: {
  canEdit: boolean
  watchlist: Watchlist
  rename: {
    onClick: (watchlist: Watchlist, autoFocus: "name") => void
  }
}) => (
  <Box
    onClick={() => canEdit && rename.onClick(watchlist, "name")}
    sx={{
      display: "flex",
      gap: 1,
      alignItems: "center",
      maxWidth: "calc(100% - 12px)",
      cursor: canEdit ? "pointer" : "default",
      "&:hover svg": {
        visibility: "visible",
      },
    }}
  >
    <EllipsisTooltip
      tooltipText={watchlist.name}
      typographyProps={{
        fontFamily: "Hanken Grotesk",
        fontWeight: 700,
        fontSize: 24,
        letterSpacing: "-0.25px",
      }}
    >
      {watchlist.name}
    </EllipsisTooltip>
    {canEdit && (
      <Edit
        fontSize="small"
        sx={{
          color: "text.secondary",
          visibility: "hidden",
        }}
      />
    )}
  </Box>
)

export { WatchlistName }
